body {
    font-weight: 400;
    font-size: 1.4rem;
    font-family: $default-font;
    color: $all-color;
    line-height: 1;
    position: relative;
    min-width: 320px;
    display: flex;
    flex-direction: column;
}
h2, h3, h4, h5, b, strong {
    font-weight: 700;
}
.title {
    font-weight: 700;
    font-size: 3.6rem;
    color: #333;
    display: block;
    text-align: center;
    line-height: 1.1;
}
.article-text {
    .title {
        margin-bottom: 30px;
    }
    p {
        font-size: 1.6rem;
        line-height: 1.25;
        text-align: justify;
    }
    a {
        color: #2643f7;
        border-bottom: 1px solid transparent;
        @include all-transition();
        &:hover {
            border-color: #2643f7;
        }
    }
}
.full-bg {
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
}
.full-width {
    max-width: 1920px;
    margin: 0 auto;
}
.all-wrapp {
    flex: 1 0 auto;
    position: relative;
    width: 100%;
}
.container {
    max-width: 1228px;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}
.row {
    display: flex;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
}
.col {
    flex-basis: 0;
	flex-grow: 1;
    padding-right: 15px;
    padding-left: 15px;       
}
.hide {
    display: none;
}
.bg-1 {
    background: #ececec;
}
.block-btn {
    text-align: center;
}
.btn {
    background: #0466e1;
    font-weight: 600;
    font-size: 1.4rem;
    color: #fff;
    letter-spacing: 0.1rem;
    text-transform: uppercase;
    display: inline-block;
    //display: inline-flex;
    //justify-content: center;
    //align-items: center;
    height: 35px;
    line-height: 34px;
    padding: 0 30px;
    border-radius: 5px;
    cursor: pointer;
    border: 1px solid transparent;
    @include link-transition();
    &:hover {
        background: #2683f7;
    }
}
@media (max-width: 767px) {
    .title {
        font-size: 3rem;
    }
}
@media (max-width: 460px) {
    .title {
        font-size: 2.4rem;
    }
    .article-text {
        p {
            font-size: 1.5rem;
            text-align: left;
        }
    }
}