.section-step {
    margin-top: 110px;
    .title {
        margin-bottom: 80px;
    }
    .block-btn {
        margin-top: 80px;
    }
}
.list-step {
    position: relative;
    counter-reset: myCounter;
    li {
        position: relative;
        &:after {
            background: #2683f7;
            font-weight: 600;
            font-size: 2.4rem;
            color: #fff;
            position: absolute;
            display: block;
            text-align: center;
            counter-increment: myCounter;
            content:counter(myCounter);
            width: 45px;
            height: 45px;
            line-height: 45px;
            border-radius: 50%;
            z-index: 2;
            top: 0;
            left: 50%;
            margin-left: -26px;
        }
    }
}
.item-step {
    text-align: center;
    .wrapp-inner {
        position: relative;
        padding-top: 67px;
        &:before {
            background: url(../img/ol.png) repeat-x;
            @include pseudo();
            width: 100%;
            height: 1px;
            /*border: 1px dashed #0d2f5c;*/
            top: 22px;
            left: calc(50% + 15px);
            z-index: 0;
        }
    }
    .item-step__desc {
        font-weight: 400;
        font-size: 1.6rem;
        display: block;
        line-height: 1.3em;
    }
    &:last-child {
        .wrapp-inner {
            &:before {
                display: none;
            }
        }
    }
}
@media (max-width: 991px) {
    .section-step {
        margin-top: 80px;
        .title {
            margin-bottom: 60px;
        }
        .block-btn {
            margin-top: 60px;
        }
    }
    .item-step {
        .item-step__desc {
            font-size: 1.4rem;
        }
    }
}
@media (max-width: 767px) {
    .list-step {
        li {
            &:after {
                font-size: 1.8rem;
                width: 35px;
                height: 35px;
                line-height: 35px;
                margin-left: -21px;
            }
        }
    }
    .item-step {
        .wrapp-inner {
            padding-top: 55px;
            &:before {
                top: 18px;
            }
        }
        .item-step__desc {
            font-size: 1.3rem;
        }
    }
}
@media (max-width: 680px) {
    .item-step {
        .wrapp-inner {
            padding-top: 54px;
            &:before {
                top: 20px;
            }
        }
        .item-step__desc {
            font-size: 1.2rem;
            line-height: 1.35em;
        }
    }
}
@media (max-width: 505px) {
    .list-step {
        justify-content: center;
        .col {
            flex: 0 0 33.33333%;
            max-width: 33.33333%;
            margin-bottom: 20px;
            &:nth-child(3) {
                .wrapp-inner {
                    &:before {
                        display: none;
                    }
                }
            }
        }
    }
}
@media (max-width: 460px) {
    .section-step {
        margin-top: 70px;
        .title {
            margin-bottom: 40px;
        }
        .block-btn {
            margin-top: 30px;
        }
    }
}
@media (max-width: 355px) {
    .list-step {
        margin: 0 -10px;
        .col {
            padding: 0 10px;
        }
    }
    .item-step .item-step__desc {
        font-size: 1.1rem;
    }
}