.section-banner {
    background-image: url('../img/bg/banner.jpg');
    position: relative;
    margin-top: 80px;
    @include all-transition();
    overflow: hidden;
    &.scroll {
        margin-top: 60px;
    }
    &::before {
        background: #000;
        @include all-pseudo();
        opacity: 0.6;
    }
    .row {
        min-height: 650px;
        padding: 50px 0;
        align-items: center;
    }
    .section-banner__text {
        flex: 0 0 56%;
        max-width: 56%;
    } 
    .block-text {
        position: relative;
        padding: 30px 20px 50px 100px;
        color: #fff;
        &::before {
            background: rgba(7,149,249,0.35);
            @include pseudo();
            width: 9000px;
            height: 100%;
            top: 0;
            right: 0;
            z-index: 0;
        }
    }
    h1 {
        font-weight: 600;
        font-size: 4.8rem;
        line-height: 1.1;
        position: relative;
    }
    .desc-warning {
        font-weight: 500;
        font-size: 2.4rem;
        color: #b31408;
        text-transform: uppercase;
        position: relative;
        display: inline-block;
        line-height: 1.1;
        margin-top: 25px;
        text-shadow: 2px 1px 2px #fff;
        //border-bottom: 1px solid #b31408;
    }
    .desc {
        font-weight: 500;
        font-size: 1.8rem;
        position: relative;
        line-height: 1.1;
        margin-top: 25px;
    }
    .section-banner__form {
        display: flex;
        justify-content: flex-end;
    }
    .box-form {
        width: 260px;
    }
}
@media (max-width: 1140px) {
    .section-banner {
        .block-text {
            padding-left: 0;
        }
    }
}
@media (max-width: 991px) {
    .section-banner {
        background-image: url(../img/bg/banner_2.jpg);
        .row {
            min-height: 550px;
            padding-top: 0;
        }
        .block-text {
            padding: 40px 15px 50px 15px;
        }
        h1 {
            font-size: 3.6rem;
        }
        .desc-warning {
            font-size: 2rem;
        }
        .desc {
            font-size: 1.6rem;
            line-height: 1.3;
        }
    }
}
@media (max-width: 767px) {
    .section-banner {
        .section-banner__text {
            flex: 0 0 100%;
            max-width: 100%;
        }
        .section-banner__form {
            justify-content: center;
            margin-top: 50px;
        }
        .block-text {
            text-align: center;
            &::before {
                left: -20px;
            }
        }
    }
}
@media (max-width: 575px) {
    .section-banner {
        margin-top: 60px;
        .block-text {
            padding: 30px 15px;
        }
        h1 {
            font-size: 3rem;
        }
        .desc-warning {
            font-size: 1.8rem;
            text-shadow: 1px 1px 1px #fff;
            margin-top: 20px;
        }
        .desc {
            font-size: 1.4rem;
            margin-top: 20px;
        }
        .section-banner__form {
            margin-top: 30px;
        }
    }
}
@media (max-width: 420px) {
    .section-banner {
        .desc-warning {
            font-size: 1.6rem;
        }
    }
}
@media (max-width: 360px) {
    .section-banner {
        .row {
            padding-bottom: 30px;
        }
        .block-text {
            padding: 25px 15px;
        }
        h1 {
            font-size: 2.8rem;
        }
        .desc-warning {
            font-size: 1.5rem;
            margin-top: 15px;
        }
        .desc {
            font-size: 1.3rem;
            margin-top: 15px;
        }
    }
}