.section-work {
    padding: 80px 0;
    margin-top: 110px;
    .title {
        margin-bottom: 85px;
    }
}
.list-work {
    .col {
        flex: 0 0 33.33333%;
        max-width: 33.33333%;
        margin-bottom: 40px;
    }
}
.item-work {
    .item-work__wrapp {
        background: #fff;
        display: block;
        height: 100%;
        @include all-transition();
        & ~ .item-work__wrapp {
            display: none;
        }
        &:hover {
            box-shadow: 0 0 15px rgba(0,0,0,0.6);
            .item-work__img {
                &::after {
                    opacity: 0.5;
                }
                img {
                    @include scale();
                }
            }
            .item-work__title {
                color: #2683f7;
            }
        }
    }
    .item-work__img {
        position: relative;
        display: block;
        line-height: 0;
        overflow: hidden;
        &::after {
            background: #000;
            @include all-pseudo();
            @include all-transition();
            opacity: 0;
        }
        img {
            width: 100%;
            object-fit: cover;
            @include img-transition();
        }
    }
    .item-work__text {
        padding: 20px 15px;
        text-align: center;
    }
    .item-work__title {
        font-weight: 600;
        font-size: 1.8rem;
        color: #333;
        display: inline-block;
        line-height: 1.1;
        height: 41px;
        overflow: hidden;
        @include link-transition();
    }
    .item-work__desc {
        font-size: 1.4rem;
        color: #333;
        display: block;
        line-height: 1.1;
        margin-top: 7px;
    }
}
@media (max-width: 991px) {
	.section-work {
        padding: 60px 0;
        margin-top: 90px;
        .title {
            margin-bottom: 65px;
        }
    }
    .list-work {
        .col {
            margin-bottom: 30px;
        }
    }
}
@media screen and (min-width: 768px) and (max-width: 991px) {
	.list-work {
        margin: 0 -5px;
        .col {
            padding: 0 5px;
        }
    }
    .item-work {
        .item-work__title {
            font-size: 1.6rem;
            height: 37px;
        }
        .item-work__desc {
            font-size: 1.3rem;
        }
    }
}
@media (max-width: 767px) {
    .list-work {
        .col {
            flex: 0 0 50%;
            max-width: 50%;
        }
    }
}
@media screen and (min-width: 541px) and (max-width: 660px) {
	.list-work {
        margin: 0 -5px;
        .col {
            padding: 0 5px;
        }
    }
    .item-work {
        .item-work__title {
            font-size: 1.6rem;
            height: 37px;
        }
        .item-work__desc {
            font-size: 1.3rem;
        }
    }
}
@media (max-width: 575px) {
	.section-work {
        padding: 45px 0;
        margin-top: 65px;
        .title {
            margin-bottom: 45px;
        }
    }
    .list-work {
        .col {
            margin-bottom: 30px;
        }
    }
}
@media (max-width: 540px) {
    .section-work {
        padding-bottom: 25px;
    }
    .list-work {
        max-width: 400px;
        margin: 0 auto;
        .col {
            flex: 0 0 100%;
            max-width: 100%;
            padding: 0;
        }
    }
}
@media (max-width: 360px) {
    .item-work {
        .item-work__title {
            font-size: 1.6rem;
            height: 37px;
        }
        .item-work__desc {
            font-size: 1.3rem;
        }
    }
}