@font-face {
	font-family: 'Inter';
	src: local("Inter Light"), local("InterLight"), url("../fonts/InterLight.woff2") format("woff2"), url("../fonts/InterLight.woff") format("woff");
	font-weight: 300;
	font-style: normal;
}

@font-face {
	font-family: 'Inter';
	src: local("Inter Regular"), local("InterRegular"), url("../fonts/InterRegular.woff2") format("woff2"), url("../fonts/InterRegular.woff") format("woff");
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: 'Inter';
	src: local("Inter Medium"), local("InterMedium"), url("../fonts/InterMedium.woff2") format("woff2"), url("../fonts/InterMedium.woff") format("woff");
	font-weight: 500;
	font-style: normal;
}

@font-face {
	font-family: 'Inter';
	src: local("Inter Semi Bold"), local("Inter-SemiBold"), url("../fonts/Inter-SemiBold.woff2") format("woff2"), url("../fonts/Inter-SemiBold.woff") format("woff");
	font-weight: 600;
	font-style: normal;
}

@font-face {
	font-family: 'Inter';
	src: local("Inter Bold"), local("InterBold"), url("../fonts/InterBold.woff2") format("woff2"), url("../fonts/InterBold.woff") format("woff");
	font-weight: bold;
	font-style: normal;
}

html {
	font-size: 10px;
	box-sizing: border-box;
	-webkit-text-size-adjust: 100%;
}

html, body {
	height: 100%;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	/*font-size: 100%;
    font: inherit;*/
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}

* {
	margin: 0;
	padding: 0;
	outline: none;
	line-height: inherit;
	min-width: 0;
	min-height: 0;
}

*,
*:before,
*:after {
	box-sizing: inherit;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

p {
	line-height: 1.2em;
	margin-bottom: 20px;
}

p:last-child {
	margin-bottom: 0;
}

a {
	text-decoration: none;
}

img {
	max-width: 100%;
	height: auto;
	max-height: 100%;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

.clearfix:after,
.clearfix:before {
	display: table;
	content: ' ';
}

.clearfix:after {
	clear: both;
}

code,
kbd,
samp {
	font-family: monospace, monospace;
	font-size: 1em;
}

body {
	font-weight: 400;
	font-size: 1.4rem;
	font-family: "Inter", Arial, Helvetica, sans-serif;
	color: #000;
	line-height: 1;
	position: relative;
	min-width: 320px;
	display: flex;
	flex-direction: column;
}

h2, h3, h4, h5, b, strong {
	font-weight: 700;
}

.title {
	font-weight: 700;
	font-size: 3.6rem;
	color: #333;
	display: block;
	text-align: center;
	line-height: 1.1;
}

.article-text .title {
	margin-bottom: 30px;
}

.article-text p {
	font-size: 1.6rem;
	line-height: 1.25;
	text-align: justify;
}

.article-text a {
	color: #2643f7;
	border-bottom: 1px solid transparent;
	transition: all 0.3s ease;
}

.article-text a:hover {
	border-color: #2643f7;
}

.full-bg {
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
}

.full-width {
	max-width: 1920px;
	margin: 0 auto;
}

.all-wrapp {
	flex: 1 0 auto;
	position: relative;
	width: 100%;
}

.container {
	max-width: 1228px;
	padding-right: 15px;
	padding-left: 15px;
	margin-right: auto;
	margin-left: auto;
}

.row {
	display: flex;
	flex-wrap: wrap;
	margin-right: -15px;
	margin-left: -15px;
}

.col {
	flex-basis: 0;
	flex-grow: 1;
	padding-right: 15px;
	padding-left: 15px;
}

.hide {
	display: none;
}

.bg-1 {
	background: #ececec;
}

.block-btn {
	text-align: center;
}

.btn {
	background: #0466e1;
	font-weight: 600;
	font-size: 1.4rem;
	color: #fff;
	letter-spacing: 0.1rem;
	text-transform: uppercase;
	display: inline-block;
	height: 35px;
	line-height: 34px;
	padding: 0 30px;
	border-radius: 5px;
	cursor: pointer;
	border: 1px solid transparent;
	transition: background 0.3s ease-in-out,border-color 0.3s ease-in-out,color 0.3s ease-in-out;
}

.btn:hover {
	background: #2683f7;
}

@media (max-width: 767px) {
	.title {
		font-size: 3rem;
	}
}

@media (max-width: 460px) {
	.title {
		font-size: 2.4rem;
	}
	.article-text p {
		font-size: 1.5rem;
		text-align: left;
	}
}

/*== begin header ==*/
.header .header-wrapp {
	background: #fff;
	position: fixed;
	width: 100%;
	top: 0;
	left: 0;
	z-index: 10;
}

.header .header-wrapp.sticky {
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
	transition: all 0.5s ease;
}

.header .header-wrapp.sticky .row {
	height: 60px;
}

.header .header-wrapp.sticky .header-contact .btn-modal {
	margin-top: 6px;
}

.header .row {
	height: 80px;
	padding: 5px 0;
	justify-content: space-between;
	align-items: center;
	transition: all 0.3s ease;
}

.header-logo {
	flex: 0 0 160px;
	max-width: 160px;
}

/* ==== MAIN MENU ===== */
.main-menu {
	position: relative;
	width: 100%;
	display: flex;
	justify-content: center;
}

.main-menu li {
	position: relative;
	display: block;
	margin-left: 25px;
}

.main-menu li:first-child {
	margin-left: 0;
}

.main-menu li a {
	font-weight: 500;
	font-size: 1.6rem;
	color: #333;
	position: relative;
	display: inline-block;
	line-height: 1.2;
	transition: all 0.3s ease;
}

.main-menu li a::after {
	background: #fa0606;
	position: absolute;
	display: block;
	content: "";
	width: 100%;
	height: 2px;
	bottom: 0;
	left: 0;
	transform: scaleX(0);
	transform-origin: center center;
	transition: all 0.3s ease;
}

.main-menu li a:hover {
	color: #fa0606;
}

.main-menu li a.active::after {
	transform: scaleX(1);
}

/* === button menu open === */
.button-menu {
	background-color: transparent;
	position: relative;
	display: block;
	width: 40px;
	line-height: 34px;
	transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1);
	backface-visibility: hidden;
	text-align: center;
	padding: 0;
	margin-top: -2px;
}

.button-menu:focus, .button-menu:hover {
	background-color: transparent;
}

.button-menu .menu-fader {
	/*font-weight: 700;
         font-size:  1.4rem;
         color: #0d2f5c;
         text-transform: uppercase;
         margin-right: 14px;*/
	display: none;
}

.button-menu span {
	background-color: #00428c;
	position: relative;
	display: inline-block;
	width: 100%;
	height: 5px;
	backface-visibility: hidden;
}

.button-menu span:before, .button-menu span:after {
	background-color: #00428c;
	position: absolute;
	left: 0;
	width: 100%;
	height: 100%;
	content: '';
	transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1);
	backface-visibility: hidden;
}

.button-menu span:before {
	transform: translate3d(0, -175%, 0);
}

.button-menu span:after {
	transform: translate3d(0, 175%, 0);
}

.button-menu.active span {
	transform: rotate(45deg);
}

.button-menu.active span:before {
	transform: rotate(180deg);
}

.button-menu.active span:after {
	transform: rotate(270deg);
}

/* === button menu open === */
.close-menu {
	display: none;
}

/*== begin lang ==*/
.header-contact {
	flex: 0 0 220px;
	max-width: 220px;
}

.header-contact .header-contact__phone {
	text-align: right;
}

.header-contact .btn-phone {
	font-weight: 700;
	font-size: 2rem;
	color: #d50b0b;
	position: relative;
	display: inline-block;
	padding-left: 25px;
	transition: background 0.3s ease-in-out,border-color 0.3s ease-in-out,color 0.3s ease-in-out;
}

.header-contact .btn-phone::before {
	position: absolute;
	display: block;
	content: "";
	top: 50%;
	left: 0;
	transform: translateY(-50%);
	background-image: url(../img/sprite.png);
	background-position: 0px 0px;
	width: 20px;
	height: 20px;
}

.header-contact .btn-phone:hover {
	color: #2683f7;
}

.header-contact .btn-modal {
	background: transparent;
	font-weight: 500;
	font-size: 1.4rem;
	color: #2643f7;
	position: relative;
	display: inline-block;
	margin-top: 10px;
	border: none;
	border-bottom: 1px solid #2643f7;
	transition: all 0.3s ease;
	cursor: pointer;
}

.header-contact .btn-modal:hover {
	border-bottom-color: transparent;
}

