/*== begin section banner ==*/
.section-banner {
	background-image: url("../img/bg/banner.jpg");
	position: relative;
	margin-top: 80px;
	transition: all 0.3s ease;
	overflow: hidden;
}

.section-banner.scroll {
	margin-top: 60px;
}

.section-banner::before {
	background: #000;
	position: absolute;
	display: block;
	content: "";
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	opacity: 0.6;
}

.section-banner .row {
	min-height: 650px;
	padding: 50px 0;
	align-items: center;
}

.section-banner .section-banner__text {
	flex: 0 0 56%;
	max-width: 56%;
}

.section-banner .block-text {
	position: relative;
	padding: 30px 20px 50px 100px;
	color: #fff;
}

.section-banner .block-text::before {
	background: rgba(7, 149, 249, 0.35);
	position: absolute;
	display: block;
	content: "";
	width: 9000px;
	height: 100%;
	top: 0;
	right: 0;
	z-index: 0;
}

.section-banner h1 {
	font-weight: 600;
	font-size: 4.8rem;
	line-height: 1.1;
	position: relative;
}

.section-banner .desc-warning {
	font-weight: 500;
	font-size: 2.4rem;
	color: #b31408;
	text-transform: uppercase;
	position: relative;
	display: inline-block;
	line-height: 1.1;
	margin-top: 25px;
	text-shadow: 2px 1px 2px #fff;
}

.section-banner .desc {
	font-weight: 500;
	font-size: 1.8rem;
	position: relative;
	line-height: 1.1;
	margin-top: 25px;
}

.section-banner .section-banner__form {
	display: flex;
	justify-content: flex-end;
}

.section-banner .box-form {
	width: 260px;
}

@media (max-width: 1140px) {
	.section-banner .block-text {
		padding-left: 0;
	}
}

@media (max-width: 991px) {
	.section-banner {
		background-image: url(../img/bg/banner_2.jpg);
	}
	.section-banner .row {
		min-height: 550px;
		padding-top: 0;
	}
	.section-banner .block-text {
		padding: 40px 15px 50px 15px;
	}
	.section-banner h1 {
		font-size: 3.6rem;
	}
	.section-banner .desc-warning {
		font-size: 2rem;
	}
	.section-banner .desc {
		font-size: 1.6rem;
		line-height: 1.3;
	}
}

@media (max-width: 767px) {
	.section-banner .section-banner__text {
		flex: 0 0 100%;
		max-width: 100%;
	}
	.section-banner .section-banner__form {
		justify-content: center;
		margin-top: 50px;
	}
	.section-banner .block-text {
		text-align: center;
	}
	.section-banner .block-text::before {
		left: -20px;
	}
}

@media (max-width: 575px) {
	.section-banner {
		margin-top: 60px;
	}
	.section-banner .block-text {
		padding: 30px 15px;
	}
	.section-banner h1 {
		font-size: 3rem;
	}
	.section-banner .desc-warning {
		font-size: 1.8rem;
		text-shadow: 1px 1px 1px #fff;
		margin-top: 20px;
	}
	.section-banner .desc {
		font-size: 1.4rem;
		margin-top: 20px;
	}
	.section-banner .section-banner__form {
