/*== begin section step work ==*/
.section-step {
	margin-top: 110px;
}

.section-step .title {
	margin-bottom: 80px;
}

.section-step .block-btn {
	margin-top: 80px;
}

.list-step {
	position: relative;
	counter-reset: myCounter;
}

.list-step li {
	position: relative;
}

.list-step li:after {
	background: #2683f7;
	font-weight: 600;
	font-size: 2.4rem;
	color: #fff;
	position: absolute;
	display: block;
	text-align: center;
	counter-increment: myCounter;
	content: counter(myCounter);
	width: 45px;
	height: 45px;
	line-height: 45px;
	border-radius: 50%;
	z-index: 2;
	top: 0;
	left: 50%;
	margin-left: -26px;
}

.item-step {
	text-align: center;
}

.item-step .wrapp-inner {
	position: relative;
	padding-top: 67px;
}

.item-step .wrapp-inner:before {
	background: url(../img/ol.png) repeat-x;
	position: absolute;
	display: block;
	content: "";
	width: 100%;
	height: 1px;
	/*border: 1px dashed #0d2f5c;*/
	top: 22px;
