.section-work {
	padding: 80px 0;
	margin-top: 110px;
}

.section-work .title {
	margin-bottom: 85px;
}

.list-work .col {
	flex: 0 0 33.33333%;
	max-width: 33.33333%;
	margin-bottom: 40px;
}

.item-work .item-work__wrapp {
	background: #fff;
	display: block;
	height: 100%;
	transition: all 0.3s ease;
}

.item-work .item-work__wrapp ~ .item-work__wrapp {
	display: none;
}

.item-work .item-work__wrapp:hover {
	box-shadow: 0 0 15px rgba(0, 0, 0, 0.6);
}

.item-work .item-work__wrapp:hover .item-work__img::after {
	opacity: 0.5;
}

.item-work .item-work__wrapp:hover .item-work__img img {
	transform: scale(1.1);
}

.item-work .item-work__wrapp:hover .item-work__title {
	color: #2683f7;
}

.item-work .item-work__img {
	position: relative;
	display: block;
	line-height: 0;
	overflow: hidden;
}

.item-work .item-work__img::after {
	background: #000;
	position: absolute;
	display: block;
	content: "";
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	transition: all 0.3s ease;
	opacity: 0;
}

.item-work .item-work__img img {
	width: 100%;
	object-fit: cover;
	transition: all 0.4s ease;
}

.item-work .item-work__text {
	padding: 20px 15px;
	text-align: center;
}

.item-work .item-work__title {
	font-weight: 600;
	font-size: 1.8rem;
	color: #333;
	display: inline-block;
	line-height: 1.1;
	height: 41px;
	overflow: hidden;
	transition: background 0.3s ease-in-out,border-color 0.3s ease-in-out,color 0.3s ease-in-out;
}

.item-work .item-work__desc {
	font-size: 1.4rem;
	color: #333;
	display: block;
	line-height: 1.1;
	margin-top: 7px;
}

