} */
.reveal-modal-bg {
	background: rgba(0, 0, 0, 0.6);
	position: fixed;
	height: 100%;
	width: 100%;
	z-index: 100;
	display: none;
	top: 0;
	left: 0;
}

.reveal-modal {
	background: rgba(17, 45, 78, 0.95);
	visibility: hidden;
	top: 80px;
	left: 50%;
	width: 400px;
	transform: translateX(-50%);
	position: absolute;
	z-index: 110;
	padding: 0 0 27px;
	overflow: hidden;
	overflow-y: auto;
	box-shadow: 0 0 2px #efefef;
}

.reveal-modal--thanks {
	color: #fff;
	text-align: center;
	font-size: 1.8rem;
	font-weight: 300;
}

.close-popap {
	font-weight: 300;
	font-size: 2.4rem;
	color: #ddd;
	cursor: pointer;
	display: block;
	position: absolute;
	overflow: hidden;
	top: 10px;
	right: 15px;
	z-index: 1;
}

.close-popap:hover {
	opacity: 0.8;
}

.thanks .close-popap {
	top: 15px;
	right: 20px;
}

@media (max-width: 420px) {
	.reveal-modal {
		width: 100%;
		left: 0;
		transform: translateX(0);
	}
}

