.header {
    .header-wrapp {
        background: #fff;
        position: fixed;
        width: 100%;
        top: 0;
        left: 0;
        z-index: 10;
        &.sticky {
            box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
            transition: all 0.5s ease;
            .row {
                height: 60px;
            }
            .header-contact {
                .btn-modal {
                    margin-top: 6px;
                }
            }
        }
    }
    .row {
        height: 80px;
        padding: 5px 0;
        justify-content: space-between;
        align-items: center;
        @include all-transition();
    }
}
.header-logo {
    flex: 0 0 160px;
    max-width: 160px;
}
/* ==== MAIN MENU ===== */
.main-menu {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    li {
        position: relative;
        display: block;
        margin-left: 25px;
        &:first-child {
            margin-left: 0;
        }
        a {
            font-weight: 500;
            font-size: 1.6rem;
            color: #333;
            position: relative;
            //text-transform: uppercase;
            display: inline-block;
            line-height: 1.2;
            @include all-transition();
            &::after {
                background: #fa0606;
                position: absolute;
                display: block;
                content: "";
                width: 100%;
                height: 2px;
                bottom: 0;
                left: 0;
                @include scale-x();
                @include transform-position();
                @include all-transition();
            }
            &:hover {
                color: #fa0606;
            }
            &.active {
                &::after {
                    @include scale-x-hover();
                }
            }
        }
    }
}
/* === button menu open === */
.button-menu {
    background-color: transparent;
    position: relative;
    display: block;
    width: 40px;
    line-height: 34px;
    transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1);
    backface-visibility: hidden;
    text-align: center;
    padding: 0;
    margin-top: -2px;
    &:focus, &:hover {
        background-color: transparent;
    }
    .menu-fader {
         /*font-weight: 700;
         font-size:  1.4rem;
         color: #0d2f5c;
         text-transform: uppercase;
         margin-right: 14px;*/
        display: none;
    }
    span {
        background-color: #00428c;
        position: relative;
        display: inline-block;
        width: 100%;
        height: 5px;
        backface-visibility: hidden;
        &:before, &:after {
            background-color: #00428c;
            position: absolute;
            left: 0;
            width: 100%;
            height: 100%;
            content: '';
            transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1);
            backface-visibility: hidden;
        }
        &:before {
            transform: translate3d(0,-175%,0);
        }
        &:after {
            transform: translate3d(0,175%,0);
        }
    }
}
.button-menu.active span {
    transform: rotate(45deg);
    &:before {
        transform: rotate(180deg);
    }
    &:after {
        transform: rotate(270deg);
    }
}
/* === button menu open === */
.close-menu {
    display: none;
}
/*== begin lang ==*/
.header-contact {
    flex: 0 0 220px;
    max-width: 220px;
    .header-contact__phone {
        text-align: right;
    }
    .btn-phone {
        font-weight: 700;
        font-size: 2rem;
        color: #d50b0b;
        position: relative;
        display: inline-block;
        padding-left: 25px;
        @include link-transition();
        &::before {
            @include sprite-pseudo();
            @include sprite($phone);
        }
        &:hover {
            color: #2683f7;
        }
    }
    .btn-modal {
        background: transparent;
        font-weight: 500;
        font-size: 1.4rem;
        color: #2643f7;
        position: relative;
        display: inline-block;
        margin-top: 10px;
        border: none;
        border-bottom: 1px solid #2643f7;
        @include all-transition();
        cursor: pointer;
        &:hover {
            border-bottom-color: transparent;
        }
    }
}
@media (max-width: 1140px) {
    .main-menu {
        li {
            a {
                font-size: 1.5rem;
            }
        }
    }
}
@media (max-width: 1040px) {
    .header-logo {
        flex: 0 0 135px;
        max-width: 135px;
    }
}
@media (max-width: 991px) {
    .header-menu {
        padding: 0;
    }
    .main-menu {
        li {
            margin-left:10px;
            a {
                font-size: 1.4rem;
            }
        }
    }
}
@media (max-width: 860px) {
    .header-contact {
        flex: 0 0 330px;
        max-width: 330px;
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }
    .header-button {
        flex: 0 0 100px;
        max-width: 100px;
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-end;
    }
    .header-menu {
        flex: 0 0 0;
        max-width: 0;
        padding: 0;
    }
    .box-main-menu {
        background: rgba(17, 45, 78, 0.9);
        position: fixed;
        top: 0;
        left: 0;
        padding: 60px 0 20px;
        margin: 0;
        width: 280px;
        height: 100%;
        text-align: left;
        z-index: 999;
        transform: translateX(-550px);
        transition: .7s;
    }
    .box-main-menu.open {
        overflow: auto;
        transform: translateX(0);
    }
    .main-menu {
        display: block;
        padding: 0 15px;
        li {
            display: block;
            text-align: left;
            margin-left: 0;
            border-bottom: 1px dotted #ccc;
            a {
                font-size: 1.6rem;
                color: #ddd;
                padding: 8px 0;
                line-height: 1.1em;
                &:after {
                    display: none;
                }
            }
        }
    }
    .close-menu {
        color: #fafafa;
        display: block;
        font-weight: 300;
        font-size: 26px;
        position: absolute;
        right: 15px;
        top: 10px;
    }
}
@media (max-width: 575px) {
    .header {
        .row {
            height: 60px;
        }
    }
    .header-logo {
        flex: 0 0 110px;
        max-width: 110px;
    }
    .header-contact {
        flex: 1 0 auto;
        max-width: 100%;
        padding-left: 0;
        .btn-phone {
            font-size: 1.6rem;
        }
        .btn-modal {
            margin-top: 6px;
        }
    }
    .header-button {
        flex: 0 0 70px;
        max-width: 70px;
    }
}
@media (max-width: 380px) {
    .header-contact {
        .btn-phone {
            font-size: 1.4rem;
            padding-left: 20px;
            &::before {
                @include sprite($phone-2);
            }
        }
        .btn-modal {
            font-size: 1.3rem;
        }
    }
    .header-button {
        flex: 0 0 60px;
        max-width: 60px;
    }
}
@media (max-width: 340px) {
    .header-logo {
        flex: 0 0 100px;
        max-width: 100px;
    }
}
