.section-about {
    margin-top: 110px;
    .col-img {
        flex: 0 0 36.5%;
        max-width: 36.5%;
    }
    .title {
        text-align: left;
    }
}
@media (max-width: 767px) {
    .section-about {
        margin-top: 80px;
    }
}
@media (max-width: 575px) {
    .section-about {
        .col-img {
            flex: 0 0 100%;
            max-width: 100%;
            text-align: center;
            margin-bottom: 45px;
        }
        .title {
            text-align: center;
        }
    }
}
@media (max-width: 460px) {
    .section-about {
        margin-top: 60px;
    }
}